import React, { useContext } from 'react';
import Fade from 'react-reveal/Fade';
import { Container } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';

const Contact = () => {
  const { contact } = useContext(PortfolioContext);
  const { email } = contact;

  return (
    <section id="contact">
      <Container>
        <Title title="Get in Touch" />
        <Fade bottom duration={1000} delay={800} distance="30px">
          <div className="contact-wrapper">
            <p className="mb-4">
              Feel free to reach out to me anytime. We can have some interesting conversations!?
            </p>
            <p className="mb-4">
               You can connect with me on any of the social links given or via mail. Just drop a Hi!
            </p>
            <a
              target="_blank"
              rel="noopener noreferrer"
              className="cta-btn cta-btn--resume"
              href={`mailto:${email}`}>
              <small> Let's Talk <i className={`fa fa-envelope-o fa-inverse`} /> </small>
            </a>
          </div>
        </Fade>
      </Container>
    </section>
  );
};

export default Contact;
